import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import {
  Link
} from "react-router-dom";
import { Typography, TextField, InputAdornment, Button } from '@material-ui/core'
import { titleChanged } from './store/view';
import { allInsulinUnitsChanged } from './store/insulinUnits';
import { allProportionateCarbsChanged } from './store/proportionateCarbs'
import mergeStates from './utils/mergeStates'
import makeOnChanged from './utils/makeOnChanged';
import makeOnBlur from './utils/makeOnBlur'
import {
  screenContainer,
  outputContainer,
  topButton,
  inputGroup,
  inputContainer,
  input
} from './utils/styles';
import {
  Maximize as MaximizeIcon,
  MenuOpen as MenuOpenIcon,
  RadioButtonUnchecked as RadioButtonUncheckedIcon,
  Cake as CakeIcon,
  DataUsage as DataUsageIcon,
  Add as AddIcon,
  Autorenew as AutorenewIcon
} from '@material-ui/icons'
import resetStates from './utils/resetStates';

export const ProportionateCarbs = () => {
  const insulinUnitsState = useSelector(state => state.insulinUnits)
  const state = useSelector(state => state.proportionateCarbs)
  const settings = useSelector(state => state.settings)
  const values = mergeStates(state, settings)
  const dispatch = useDispatch()
  const onChanged = makeOnChanged(state, settings, dispatch, allProportionateCarbsChanged)
  const onBlur = makeOnBlur(settings, dispatch, allProportionateCarbsChanged)

  useEffect(() => {
    dispatch(
      titleChanged('Carbohydrates')
    )
  })

  return (
    <section className="proportionatecarbs" style={screenContainer}>

      <div className="proportionatecarbs__output" style={outputContainer}>
        <Typography variant="h2" noWrap>
          {state.roundedConsumedCarbs||0}g
        </Typography>
      </div>

      <div className="proportionatecarbs__button__addtoinsulin">
        <Button
          style={topButton}
          component={Link} to="/insulinunits"
          onClick={
            () => {
              const value = parseFloat(insulinUnitsState.ui_consumedCarbs)
                ? `${insulinUnitsState.ui_consumedCarbs}+${state.roundedConsumedCarbs||0}`
                : `${state.roundedConsumedCarbs||0}`
              dispatch(allInsulinUnitsChanged(mergeStates(
                insulinUnitsState,
                settings,
                {
                  'consumedCarbs': value,
                  'ui_consumedCarbs': value
                }
              )))
            }
          }
        >
          <AddIcon />
          &nbsp;Add to insulin calculator
        </Button>
      </div>

      <div className="proportionatecarbs__button__reset">
        <Button
          style={topButton}
          onClick={
            () => {
              dispatch(allProportionateCarbsChanged(resetStates(
                state,
                settings
              )))
            }
          }
        >
          <AutorenewIcon />
          &nbsp;Reset
        </Button>
      </div>

      <div className="proportionatecarbs_input" style={inputGroup}>

        <div className="proportionatecarbs__input__columnweight" style={inputContainer}>
          <TextField label="Per weight"
            inputProps={{style: input}}
            onChange={onChanged.bind(this, 'columnWeight')}
            onBlur={onBlur.bind(this, 'columnWeight')}
            value={values.ui_columnWeight}
            InputProps={{
              startAdornment: <InputAdornment position="start"><MaximizeIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">g</InputAdornment>,
            }}
          />
        </div>

        <div className="proportionatecarbs__input__carbspercolumnweight" style={inputContainer}>
          <TextField label="Carbs per weight"
            inputProps={{style: input}}
            onChange={onChanged.bind(this, 'carbsPerColumnWeight')}
            onBlur={onBlur.bind(this, 'carbsPerColumnWeight')}
            value={values.ui_carbsPerColumnWeight}
            InputProps={{
              startAdornment: <InputAdornment position="start"><MenuOpenIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">g</InputAdornment>,
            }}
          />
        </div>

        <div className="proportionatecarbs__input__totalpacketweight" style={inputContainer}>
          <TextField label="Total (net) weight"
            inputProps={{style: input}}
            onChange={onChanged.bind(this, 'totalPacketWeight')}
            onBlur={onBlur.bind(this, 'totalPacketWeight')}
            value={values.ui_totalPacketWeight}
            InputProps={{
              startAdornment: <InputAdornment position="start"><CakeIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">g</InputAdornment>,
            }}
          />
        </div>

        <div className="proportionatecarbs__input__totalpieces" style={inputContainer}>
          <TextField label="Total pieces"
            inputProps={{style: input}}
            onChange={onChanged.bind(this, 'totalPieces')}
            onBlur={onBlur.bind(this, 'totalPieces')}
            value={values.ui_totalPieces}
            InputProps={{
              startAdornment: <InputAdornment position="start"><RadioButtonUncheckedIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">{values.ui_consumedPieces}/{values.ui_totalPieces}</InputAdornment>,
            }}
          />
        </div>

        <div className="proportionatecarbs__input__consumedpieces" style={inputContainer}>
          <TextField label="Consumed pieces"
            inputProps={{style: input}}
            onChange={onChanged.bind(this, 'consumedPieces')}
            onBlur={onBlur.bind(this, 'consumedPieces')}
            value={values.ui_consumedPieces}
            InputProps={{
              startAdornment: <InputAdornment position="start"><DataUsageIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">{values.ui_consumedPieces}/{values.ui_totalPieces}</InputAdornment>,
            }}
          />
        </div>

      </div>

    </section>
  )
}
