import React, { useEffect } from 'react';
import { defaultSettings } from './store/initialState';
import { useDispatch, useSelector } from 'react-redux'
import { TextField, InputAdornment, } from '@material-ui/core';
import { titleChanged } from './store/view';
import { allSettingsChanged } from './store/settings'
import makeOnChanged from './utils/makeOnChanged';
import makeOnBlur from './utils/makeOnBlur'
import {
  screenContainer,
  input,
  inputContainer
} from './utils/styles';
import {
  LocalHospitalOutlined as LocalHospitalOutlinedIcon,
  Restaurant as RestaurantIcon,
  ShowChart as ShowChartIcon,
  FitnessCenter as FitnessCenterIcon,
  Settings as SettingsIcon,
  RadioButtonUnchecked as RadioButtonUncheckedIcon,
  Cake as CakeIcon,
  DataUsage as DataUsageIcon,
} from '@material-ui/icons'

export const Settings = () => {
  const settings = useSelector(state => state.settings)
  const dispatch = useDispatch()
  const onChanged = makeOnChanged(settings, defaultSettings, dispatch, allSettingsChanged, { noui: false })
  const onBlur = makeOnBlur(defaultSettings, dispatch, allSettingsChanged, { noui: false })

  useEffect(() => {
    dispatch(
      titleChanged('Settings')
    )
  })

  return (
    <section className="settings" style={screenContainer}>

      <div className="settings_input">

        <div className="settings__input__currentbg" style={inputContainer}>
          <TextField label="Default current blood glucose"
            inputProps={{style: input}}
            onChange={onChanged.bind(this, 'currentBg')}
            onBlur={onBlur.bind(this, 'currentBg')}
            value={settings.ui_currentBg}
            InputProps={{
              startAdornment: <InputAdornment position="start"><ShowChartIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">mmol/l</InputAdornment>,
            }}
          />
        </div>

        <div className="settings__input__targetbg" style={inputContainer}>
          <TextField label="Default target blood glucose"
            inputProps={{style: input}}
            onChange={onChanged.bind(this, 'targetBg')}
            onBlur={onBlur.bind(this, 'targetBg')}
            value={settings.ui_targetBg}
            InputProps={{
              startAdornment: <InputAdornment position="start"><SettingsIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">mmol/l</InputAdornment>,
            }}
          />
        </div>

        <div className="settings__input__unitadjustmentpercentage" style={inputContainer}>
          <TextField label="Default adjustment"
            inputProps={{style: input}}
            onChange={onChanged.bind(this, 'unitAdjustmentPercentage')}
            onBlur={onBlur.bind(this, 'unitAdjustmentPercentage')}
            value={settings.ui_unitAdjustmentPercentage}
            InputProps={{
              startAdornment: <InputAdornment position="start"><FitnessCenterIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">%</InputAdornment>,
            }}
          />
        </div>

        <div className="settings__input__carbsperunit" style={inputContainer}>
          <TextField label="Default carbs per unit"
            inputProps={{style: input}}
            onChange={onChanged.bind(this, 'carbsPerUnit')}
            onBlur={onBlur.bind(this, 'carbsPerUnit')}
            value={settings.ui_carbsPerUnit}
            InputProps={{
              startAdornment: <InputAdornment position="start"><SettingsIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">g</InputAdornment>,
            }}
          />
        </div>

        <div className="settings__input__bgperunit" style={inputContainer}>
          <TextField label="Default blood glucose correction per unit"
            inputProps={{style: input}}
            onChange={onChanged.bind(this, 'bgPerUnit')}
            onBlur={onBlur.bind(this, 'bgPerUnit')}
            value={settings.ui_bgPerUnit}
            InputProps={{
              startAdornment: <InputAdornment position="start"><SettingsIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">mmol/l</InputAdornment>,
            }}
          />
        </div>

        <div className="settings__input__totalpieces" style={inputContainer}>
          <TextField label="Default total pieces"
            inputProps={{style: input}}
            onChange={onChanged.bind(this, 'totalPieces')}
            onBlur={onBlur.bind(this, 'totalPieces')}
            value={settings.ui_totalPieces}
            InputProps={{
              startAdornment: <InputAdornment position="start"><RadioButtonUncheckedIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">{settings.consumedPieces}/{settings.totalPieces}</InputAdornment>,
            }}
          />
        </div>

        <div className="settings__input__consumedpieces" style={inputContainer}>
          <TextField label="Default consumed pieces"
            inputProps={{style: input}}
            onChange={onChanged.bind(this, 'consumedPieces')}
            onBlur={onBlur.bind(this, 'consumedPieces')}
            value={settings.ui_consumedPieces}
            InputProps={{
              startAdornment: <InputAdornment position="start"><DataUsageIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">{settings.consumedPieces}/{settings.totalPieces}</InputAdornment>,
            }}
          />
        </div>

        <div className="settings__input__minimumcarbs" style={inputContainer}>
          <TextField label="Minimum carbs"
            inputProps={{style: input}}
            onChange={onChanged.bind(this, 'minimumCarbs')}
            onBlur={onBlur.bind(this, 'minimumCarbs')}
            value={settings.ui_minimumCarbs}
            InputProps={{
              startAdornment: <InputAdornment position="start"><SettingsIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">g</InputAdornment>,
            }}
          />
        </div>

        <div className="settings__input__minimumunits" style={inputContainer}>
          <TextField label="Minimum units"
            inputProps={{style: input}}
            onChange={onChanged.bind(this, 'minimumUnits')}
            onBlur={onBlur.bind(this, 'minimumUnits')}
            value={settings.ui_minimumUnits}
            InputProps={{
              startAdornment: <InputAdornment position="start"><SettingsIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">unit</InputAdornment>,
            }}
          />
        </div>


      </div>

    </section>
  )
}
